:root {
  --main-bg-color: #f3f3f3;
  --main-fg-color: #cb262e;
}

body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input,
textarea,
button,
body {
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fc-license-message {
  display: none;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.main-menu > a {
  color: var(--main-bg-color);
  text-decoration: none;
  font-size: 1rem;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0.5rem 0 0.5rem;
  padding: 0.3rem 0 0.3rem 0;
  border: 2px solid var(--main-fg-color);
  border-radius: 1rem;
  transition-duration: 300ms;
}

.main-menu > *:hover {
  border: 2px solid var(--main-bg-color);
}

@media only screen and (max-width: 600px) {
  .main-menu {
    justify-content: space-between;
    width: 100%;
  }
}

.section-main {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--main-bg-color);
  /* color: var(--main-bg-color); */
  /* font-size: 2rem; */
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
}

.section-header {
  font-size: 2rem;
  padding: 1rem;
  margin: 1.5rem;
  border-bottom: 1px solid var(--main-fg-color);
  display: flex;
  justify-content: space-between;
}

.section-container {
  margin: 1.5rem;
}

.section-container::before {
  display: block;
  content: ' ';
  margin-top: -60px;
  height: 60px;
  visibility: hidden;
  pointer-events: none;
}

.main-title {
  font-size: 3rem;
  text-align: center;
  text-transform: uppercase;
}

@media only screen and (max-width: 600px) {
  .not-top .main-title {
    display: none;
  }
}

.image {
  background-image: url('https://via.placeholder.com/1600x800?text=Robs+Reports');
  height: 300px;
  width: 100%;
  background-attachment: fixed;
  alt: 'Image';
}

.appointment {
  flex-wrap: nowrap !important;
}

@media only screen and (max-width: 600px) {
  .appointment #calendar {
    display: none;
  }
}

.form-main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
  /* max-width: 800px; */
}

.form-main label {
  margin-top: 0.8rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.form-main input,
.form-main textarea {
  border-radius: 1rem;
  border: 1px solid var(--main-fg-color);
  padding: 0.8rem;
  transition-duration: 300ms;
}

.form-main input:focus,
.form-main textarea:focus {
  box-shadow: 0px 1px 5px 0px var(--main-bg-color);
}

.form-main input::-webkit-input-placeholder,
.form-main textarea::-webkit-input-placeholder {
  color: #aaaaaa;
}

.form-main input::-ms-input-placeholder,
.form-main textarea::-ms-input-placeholder {
  color: #aaaaaa;
}

.form-main input::placeholder,
.form-main textarea::placeholder {
  color: #aaaaaa;
}

.input-group {
  width: 300px;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  flex-direction: column;
}

.main-button {
  background-color: var(--main-fg-color);
  color: var(--main-bg-color);
  border-radius: 1rem;
  border: none;
  margin: 0.8rem;
  padding: 1rem;
  cursor: pointer;
  transition-duration: 300ms;
}

.main-button:hover {
  color: var(--main-bg-color);
  background-color: var(--main-fg-color);
  box-shadow: 0px 1px 5px 0px var(--main-fg-color);
}

.main-button[disabled]:hover {
  box-shadow: 0px 1px 5px 0px gray;
}

.main-button:disabled {
  background-color: gray;
}

.main-button:active {
  background-color: var(--main-fg-color);
}

.booking-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.booking-submit-button {
  margin-left: 15%;
  margin-right: 15%;
  width: 98%;
  margin-left: 1%;
  margin-right: 1%;
}

.collapse-section-container .section-header {
  cursor: pointer;
}

.collapse-section-container {
  width: 100%;
}

.header {
  background-color: var(--main-fg-color);
  color: var(--main-bg-color);
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
  transition-duration: 300ms;
  /* height: 80px; */
}

.header.top {
  flex-direction: column;
  justify-content: center;
  /* height: 150px; */
}

.main-title .header {
  font-size: 1rem;
}

.top .main-title {
  font-size: 4rem;
}

.top .main-menu {
  font-size: 3rem;
}

@media only screen and (max-width: 600px) {
  .header {
    justify-content: center !important;
  }
}

.main-page {
  padding-top: 8rem;
}

